/*
 * automatically open partner overlay based on hash value
 */
(function ($) {
	"use strict";

	$(document).ready(function () {
		// get hash value
		var hash = "";
		if (window.location.hash) {
			/* Puts hash in variable, and removes the # character */
			hash = window.location.hash.substring(1);
			/* remove initial slash if it exists */
			hash = hash.indexOf("/") == 0 ? hash.substring(1) : hash;
		}

		var partnerName = hash.replace("partner/", "");

		if(partnerName == "qualys-ticket-notification-engine"){
			document.location.href = "https://community.qualys.com/api";
		}

		if (partnerName) {
			// open category
			$("."+partnerName).parents(".q-partners-filter__content").addClass("q-partners-filter__content--active");

			// get partner element
			var element = document.querySelectorAll("."+partnerName + " a");
			// convert it from a nodelist to an array
			element = Array.prototype.slice.call(element);

			// if the element is found
			if ($.isArray(element) && element.length > 0) {
				// trigger a click event on the element using native JS (not jQuery)
				var event = document.createEvent("MouseEvent");
				event.initEvent("click", true, true);
				element[0].dispatchEvent(event);
			}
		}
	});
}(jQuery));
